const Search = {
   init: function() {
      const searchHeaderBtn = document.querySelector("#SearchToggle");
      const searchHeaderClose = document.getElementById("CloseSearch");
      const searchPageBtn = document.querySelector("#SearchBox");
   
      searchHeaderBtn.addEventListener("click", this.showSearchMobile);
      if (searchPageBtn) { searchPageBtn.addEventListener("click", this.showSearchMobile); }
      searchHeaderClose.addEventListener("click", this.closeSearchMobile);
   },
   
   showSearchMobile: function() {
      if (window.matchMedia("(max-width: 1200px)").matches) {
         const searchForm = document.querySelector(".js-search-form");
         const searchHeaderInput = document.getElementById("boost-pfs-search-box-0");
         const header = document.querySelector(".header-outer");

         if (window.matchMedia("(min-width: 768px)").matches) {
            document.getElementById("CloseSearch").style.display = "block";
            document.querySelector(".modal-background").style = "display: block; z-index: 99";
         }

         searchHeaderInput.style.display = "block";
         searchHeaderInput.focus();

         const searchBoostInput = document.querySelector(".boost-pfs-search-suggestion-mobile-top-panel");
         if (searchBoostInput) {
            searchBoostInput.style.top = header.offsetHeight + "px";
            searchForm.appendChild(searchBoostInput);
         }

         const suggestionWrapper = [...document.querySelectorAll(".boost-pfs-search-suggestion-wrapper")];
         suggestionWrapper.forEach(item => {
            searchForm.appendChild(item);
         })
      }
   },
   
   closeSearchMobile: function() {
      if (window.matchMedia("(max-width: 1200px)").matches) {
         const searchHeaderInput = document.getElementById("boost-pfs-search-box-0");
         const searchHeaderClose = document.getElementById("CloseSearch");
         const modalBackground = document.querySelector(".modal-background");
         
         try {
            searchHeaderInput.style = null;
            searchHeaderClose.style = null;
            modalBackground.style = null;
         }
         catch(e){}
      }
   }   
}

window.Search = Search;

document.addEventListener("DOMContentLoaded", () => {
   Search.init();
})

export default Search;