/**
 * Closes a megamenu
 * @param {Element} megamenu - The containing <li> element of the megamenu that you would like to close
 */
function closeMegamenu(megamenu) {
  megamenu.classList.remove("show")
  megamenu.querySelector(".dropdown-toggle").setAttribute("aria-expanded", false)
  megamenu.querySelector(".megamenu").classList.remove("show")
}

/**
 * Opens a megamenu
 * @param {Element} megamenu - The containing <li> element of the megamenu that you would like to open
 */
function openMegamenu(megamenu) {
  megamenu.classList.add("show")
  megamenu.querySelector(".dropdown-toggle").setAttribute("aria-expanded", true)
  megamenu.querySelector(".megamenu").classList.add("show")
}

// Open megamenu on hover (Desktop)
function handleHover(thisMegamenu) {
  if (window.matchMedia("(min-width: 1201px)").matches) {
    thisMegamenu.addEventListener("mouseover", handleEventOpen)
    thisMegamenu.addEventListener("mouseout", handleEventClose)
  }
  else {
    thisMegamenu.removeEventListener("mouseover", handleEventOpen)
    thisMegamenu.removeEventListener("mouseout", handleEventClose)
  }
}

function handleEventOpen(e) {
  let thisMegamenu = e.currentTarget;
  openMegamenu(thisMegamenu);
}

function handleEventClose(e) {
  let thisMegamenu = e.currentTarget;
  closeMegamenu(thisMegamenu);
}

function closeThirdLevel(thisMenu, thisToggle, megamenus) {
  let mobileNavWrapper = document.querySelector("[mobile-nav-wrapper]");
  mobileNavWrapper.classList.remove("third-level");
  thisMenu.classList.remove("show")
  thisToggle.classList.remove("active");

  let backBtn = document.querySelector(".nav__back-btn");
  let parentName = mobileNavWrapper.querySelector(".show").querySelector(".megamenu__link-title").textContent;
  backBtn.querySelector(".nav__back-txt").textContent = backBtn.querySelector(".nav__back-txt").textContent.replace(" " + parentName, "");
  backBtn.onclick = () => closeSecondLevel(megamenus);
}

function closeSecondLevel(megamenus) {
    let mobileNav = document.querySelector("#MobileNav");      
    let mobileNavWrapper = document.querySelector("[mobile-nav-wrapper]");

    if (mobileNav.classList.contains("is-closing")) {
      mobileNav.classList.remove("is-closing");
      mobileNavWrapper.classList.remove("is-closing");
    }
    
    for (let thisMegamenu of megamenus) {
      closeMegamenu(thisMegamenu)
    }
}

document.addEventListener("DOMContentLoaded", function () {
  // Get all megamenus
  let megamenus = Array.from(document.querySelectorAll(".has-megamenu"))

  // Loop each megamenu and add a click event listener to the containing <li> element
  for (let thisMegamenu of megamenus) {
    handleHover(thisMegamenu);
    window.addEventListener("resize", () => handleHover(thisMegamenu));

    thisMegamenu.addEventListener("click", function (e) {

      // Check if this megamenu is already open
      var thisMenuOpen = thisMegamenu.classList.contains("show")

      // Close all other megamenus
      for (let megamenu of megamenus) {
        closeMegamenu(megamenu)
      }

      // Toggle opened state of this megamenu
      if (thisMenuOpen) {
        closeMegamenu(thisMegamenu)
      } else {
        openMegamenu(thisMegamenu)
      }

      let mobileNav = document.querySelector("#MobileNav");      
      let mobileNavWrapper = document.querySelector("[mobile-nav-wrapper]");

      mobileNav.classList.add("is-closing");
      mobileNavWrapper.classList.add("is-closing");

      let backBtn = document.querySelector(".nav__back-btn");
      backBtn.onclick = () => closeSecondLevel(megamenus);
    })
  }

  // Adds trigger to toggle mobile menu on burger icon
  document.querySelector('[data-trigger]').addEventListener("click", function () {
    let triggerId = this.dataset.trigger
    let thisMenu = this.parentElement.querySelector(triggerId)
    let thisMenuOpen = thisMenu.classList.contains("show")
    if (thisMenuOpen) {
      thisMenu.classList.remove("show")
      document.body.classList.remove("offcanvas-active")
    } else {
      thisMenu.classList.add("show")
      document.body.classList.add("offcanvas-active")
    }
  })

  // Adds trigger to mobile menu close button
  document.querySelector('.close-mobile-menu').addEventListener("click", function () {
    this.parentElement.parentElement.classList.remove("show")
    document.body.classList.remove("offcanvas-active")
  })

  // Get mobile third level menu toggles
  let mobileToggles = Array.from(document.querySelectorAll(".mobile-child-toggle"))

  // Loop each mobile third level toggle and add event listener
  for (let thisToggle of mobileToggles) {
    thisToggle.addEventListener("click", function (e) {
      let backBtn = document.querySelector(".nav__back-btn");
      let mobileNavWrapper = document.querySelector("[mobile-nav-wrapper]");
      let thisMenu = thisToggle.nextElementSibling;

      if (thisMenu && !thisMenu.classList.contains("show")) {
        thisMenu.classList.add("show")
        thisToggle.classList.add("active");
        mobileNavWrapper.classList.add("third-level");
        
        // Change text of back button
        let parentName = mobileNavWrapper.querySelector(".show").querySelector(".megamenu__link-title").textContent;
        backBtn.querySelector(".nav__back-txt").textContent += (" " + parentName);

        // Adds trigger to mobile back button
        backBtn.onclick = () => closeThirdLevel(thisMenu, thisToggle, megamenus);
      }
    })
  }

  // Prevent the menu from closing itself when it is clicked open
  Array.from(document.querySelectorAll(".has-megamenu")).forEach(element => {
    element.addEventListener('click', function (e) {
      e.stopPropagation()
    })
  })

  // Prevent the menu from closing itself when it is clicked inside
  Array.from(document.querySelectorAll(".megamenu")).forEach(element => {
    element.addEventListener('click', function (e) {
      e.stopPropagation()
    })
  })

  // Close open megamenus when anywhere on the page is clicked
  // document.addEventListener("click", function () {
  //   for (let thisMegamenu of megamenus) {
  //     closeMegamenu(thisMegamenu)
  //   }
  // })
})
