const Blogs = {
   state: {
      currentPage: 1,
      maxPage: 1,
      tag: null,
      sectionContainer: null
   },

   init: function() {
      this.state.sectionContainer = document.querySelector(".blog-list-section");

      if (this.state.sectionContainer) {
         this.state.maxPage = this.state.sectionContainer.getAttribute("no-page");
      
         let loadMoreBtn = document.querySelector(".js-blog-loadmore");
         loadMoreBtn.addEventListener("click", () => {
            this.state.currentPage = this.state.currentPage + 1;
            this.loadMore(this.state.currentPage, this.state.tag);
            loadMoreBtn.blur();
         }); 
   
         this.initNav();
      }
   },

   initNav: function() {
      let navigator = document.querySelector(".blog-list__nav-list");
      $(navigator).slick({
         slidesToShow: 1,
         variableWidth: true,
         focusOnSelect: true,
         dots: false,
         arrows: false,
         infinite: true,
         swipeToSlide: true,
         touchThreshold: 1000
      })

      let navItems = [...document.querySelectorAll(".blog-list__nav-item")];
      navItems.forEach(item => {
         item.addEventListener("click", () => {
            // Remove current selected
            let currentSelected = navItems.filter(item => item.className.includes("selected"))[0];
            currentSelected.classList.remove("selected");

            // Add new selected
            this.state.tag = item.getAttribute("tag");
            this.state.currentPage = 1;
            item.classList.add("selected");
            this.loadTag(this.state.tag);

            // Show load more button
            let loadMoreBtn = document.querySelector(".js-blog-loadmore");
            loadMoreBtn.style = null;
         })
      })
   },

   loadTag: async function(tag) {
      let currentPath = window.location.pathname;
      let articleContainer = this.state.sectionContainer.querySelector(".blog-list__blog");
      let query = tag ? `${currentPath}/tagged/${tag}` : currentPath;

      await fetch(query)
      .then(res => res.text())
      .then(res => {
         let parser = new DOMParser();
         let dom = parser.parseFromString(res, 'text/html');
         let newArticleContainer = dom.querySelector(".blog-list__blog");
         articleContainer.replaceWith(newArticleContainer);
         articleContainer.classList.remove("hide-first");
      })
   },

   loadMore: async function(page, tag) {
      let currentPath = window.location.pathname;
      let articleContainer = this.state.sectionContainer.querySelector(".blog-list__blog");
      let path = tag ? `${currentPath}/tagged/${tag}` : currentPath;
      
      await fetch(`${path}?page=${page}`)
      .then(res => res.text())
      .then(res => {
         let parser = new DOMParser();
         let dom = parser.parseFromString(res, 'text/html');
         let articles = [...dom.querySelectorAll(".blog-list__article")];

         if (articles.length > 0){
            articles.map(item => {
               articleContainer.appendChild(item);
            })
         }
         else {
            let loadMoreBtn = document.querySelector(".js-blog-loadmore");
            loadMoreBtn.style.display = "none";
         }
      })
   }
}

window.Blogs = Blogs;
window.addEventListener("DOMContentLoaded", () => {
   Blogs.init();
})