// import Swiper bundle with all modules installed
import Swiper, { Navigation, Pagination, Autoplay, Scrollbar, Thumbs } from "swiper/core";
Swiper.use([Navigation, Pagination, Scrollbar, Autoplay, Thumbs]);

// init Swiper:

document.addEventListener("DOMContentLoaded", function () {
  const slider_1 = document.querySelector('.sliderMain');
  const slider_2 = document.querySelector('.sliderThumb');
  const slider_3 = document.querySelector('.giftSlider');
  const slider_4 = document.querySelector('.listProductSlider');

  if (slider_1 != null && slider_2 != null) {
    $('.sliderMain').slick({
     slidesToShow: 1,
     slidesToScroll: 1,
     arrows: true,
     asNavFor: '.sliderThumb',
     infinite: true,
     draggable: false,
     responsive: [
        {
          breakpoint: 767,
          settings: {
            infinite: true,
            arrows: false,
            dots: true,
            draggable: true
          }
        },
      ]
    });

    $('.sliderThumb').slick({
     slidesToShow: 5,
     slidesToScroll: 1,
     asNavFor: '.sliderMain',
     arrows: false,
     vertical:true,
     focusOnSelect: true,
     adaptiveHeight: true,
     infinite: true,
     draggable: false,
     arrows: true
    });

    $('.sliderMain').on('beforeChange', function(event, slick, currentSlide, nextSlide){
      const videos = [...document.querySelectorAll('.video')];

      if (videos.length > 0) {
        videos.forEach(item => {
          item.classList.remove('playingVideo');
          item.parentElement.querySelector('.play-video').removeAttribute("hidden");
          item.style.maxHeight = $('.sliderMain .img-prod').height()+'px';
          item.pause();
        });
      }

      if ($(window).width() > 767 && $('.sliderThumb').hasClass("active-thumb")) {
        setTimeout(function(){
          let heightSlide = $('.sliderMain .img-prod').height();
          const str = `<style>.sliderThumb .slick-list{height: ${heightSlide}px !important}</style>`;

          $('.prod-img-thumb').find('style').remove();
          $('.prod-img-thumb').append(str)
        }, 1000);
      }
    });

    const optionSelectors = [...document.querySelectorAll('.pdp-option-select[data-option="option1"]')];
    const indexImg = [...document.querySelectorAll('.img-prod')];

    optionSelectors.forEach(optionSelector => {
      optionSelector.addEventListener('change', function(){
        let linkImg = this.getAttribute("data-img");

        if (linkImg != '') {
          indexImg.forEach(img => {
            const imgLink = img.getAttribute('src');

            if (imgLink.includes(linkImg)) {
              const index = img.getAttribute("data-index");
              $('.sliderMain').slick('slickGoTo', index);
            }
          });
        }
      });
    });
  }

  if (slider_3 != null) {
    let giftSlider, markGiftSlider;

    if (screen.width > 767) {
      if (!markGiftSlider) {
        giftSlider = new Swiper(".giftSlider", {
          spaceBetween: 20,
          slidesPerView: 2.5,
          watchSlidesProgress: true,
          autoHeight: true,
          navigation: {
            nextEl: ".swiper-button-next",
            prevEl: ".swiper-button-prev",
          },
        });

        markGiftSlider = true;
      }
    }

    window.addEventListener('resize', function(){
      const screenWindow = screen.width;

      if (screenWindow > 767) {
        if (!markGiftSlider) {
          giftSlider = new Swiper(".giftSlider", {
            spaceBetween: 20,
            slidesPerView: 2.5,
            watchSlidesProgress: true,
            autoHeight: true,
            navigation: {
              nextEl: ".swiper-button-next",
              prevEl: ".swiper-button-prev",
            },
          });

          markGiftSlider = true;
        }
      } else {
        if (markGiftSlider) {
          giftSlider.destroy();
          markGiftSlider = false;
        }
      }
    });
  }

  if (slider_4 != null) {
    let listProductSlider, markProductSlider;

    if (screen.width > 767) {
      if (!markProductSlider) {
        listProductSlider = new Swiper(".listProductSlider", {
          spaceBetween: 20,
          slidesPerView: 3.2,
          watchSlidesProgress: true,
          loop: true,
          navigation: {
            nextEl: ".swiper-button-next",
            prevEl: ".swiper-button-prev",
          },
        });

        markProductSlider = true;
      }
    }

    window.addEventListener('resize', function(){
      const screenWindow = screen.width;

      if (screenWindow > 767) {
        if (!markProductSlider) {
          listProductSlider = new Swiper(".listProductSlider", {
            spaceBetween: 20,
            slidesPerView: 3.2,
            watchSlidesProgress: true,
            loop: true,
            navigation: {
              nextEl: ".swiper-button-next",
              prevEl: ".swiper-button-prev",
            },
          });

          markProductSlider = true;
        }
      } else {
        if (markProductSlider) {
          listProductSlider.destroy();
          markProductSlider = false;
        }
      }
    });
  }
})
