const Footer = {
   state: {
      isMobile: false
   },

   init: function() {
      let footer = document.querySelector("#Footer");   
      let accordions = [...footer.querySelectorAll(".footer__accordion")];

      // Add event listener for accordion
      accordions.forEach(accordion => {
         let toggler = accordion.firstElementChild;
         toggler.addEventListener("click", this.toggleAccordion);
      })

      this.state.isMobile = window.matchMedia("(max-width: 899px)").matches;
   },

   toggleAccordion: function(e) {
      if (window.matchMedia("(max-width: 899px)").matches) {
         e.currentTarget.parentNode.classList.toggle("expand");
         $(e.currentTarget.nextElementSibling).slideToggle();
      }
   },

   resetStyle: function() {
      if (window.matchMedia("(max-width: 899px)").matches != this.state.isMobile) {
         this.state.isMobile = window.matchMedia("(max-width: 899px)").matches;
         let footer = document.querySelector("#Footer");   
         let accordions = [...footer.querySelectorAll(".footer__accordion")];
         accordions.forEach(accordion => {
            let toggler = accordion.firstElementChild;
            accordion.classList.remove("expand");
            toggler.nextElementSibling.style= null;
         })
      }
   }
}

window.Footer = Footer;

document.addEventListener("DOMContentLoaded", () => {
   Footer.init();
})

window.addEventListener("resize", () => Footer.resetStyle());
