const Cart = {   
   initialize: function() {
      const container = document.querySelector("#PageContent");
      const modalOpener = document.querySelectorAll(".js-cart-modal-opener");
      
      if (modalOpener) {
         modalOpener.forEach(opener => {
            let modalID = opener.getAttribute("target");
            let modal = document.getElementById(modalID);
            let modalCloseBtn = modal.querySelector(".js-close-gift-modal");
            
            // Move the modal to outside
            container.appendChild(modal);

            // Trigger to open modal
            opener.addEventListener("click", () => {   
               this.openGiftModal(modal);
            })

            // Trigger to close modal
            modalCloseBtn.addEventListener("click", () => {
               this.closeGiftModal(modal);
            })
         })
      }
      this.initWordCount();
   },

   initWordCount: function() {
      let noteForm = [...document.querySelectorAll(".js-note-form")];

      if (noteForm) {
         noteForm.forEach(form => {
            let textField = form.getElementsByTagName("textarea")[0];
            let wordCount = form.querySelector(".js-word-count");
   
            textField.addEventListener("keyup", () => {
               let count = textField.value.split("").length;
               wordCount.textContent = 300 - count;

               if (count > 0) {
                  form.querySelector(".js-submit-msg").disabled = false;
               }
               else {
                  form.querySelector(".js-submit-msg").disabled = true;
               }
            })
         })
      }
   },

   openGiftModal: function(modal) {
      modal.classList.add("active");
      document.querySelector(".modal-background").style.display = "block";
   },

   closeGiftModal: function(modal) {
      modal.classList.remove("active");
      document.querySelector(".modal-background").style = null;
   }
}

window.Cart = Cart;
document.addEventListener("DOMContentLoaded", () => {
   Cart.initialize();
});

export default Cart;