document.addEventListener("DOMContentLoaded", function () {
  //feature open tab
  let tabClick = [...document.querySelectorAll(".js-accordion")];
  for (let thisClick of tabClick) {
    thisClick.addEventListener("click", function (e) {
      const parent = this.parentElement;

      if ([...parent.classList].includes('active')) {
        parent.classList.remove("active");
        parent.children[1].style.removeProperty("height");
      }else{
        const heightContent = e.currentTarget.parentElement.querySelector(".content--inner").clientHeight;

        parent.classList.add("active");
        e.currentTarget.parentElement.querySelector(".accordion-content").style.height = `${heightContent}px`;
      }
    })
  }

  //scroll to size guide
  const sizeGuide = document.querySelector('.js-sizeGuide');

  if (sizeGuide != null) {
    sizeGuide.addEventListener('click', function(e){
      e.preventDefault();

      if (document.getElementById('size-guide') != null) {
        window.scrollTo({
          top: document.getElementById('size-guide').offsetTop - 150,
          behavior: 'smooth'
        });
      }
    });
  }

  //clisk playvideo
  const btnPlayVideos = [...document.querySelectorAll('.js-playVideos')];

  if (btnPlayVideos.length > 0) {
    btnPlayVideos.forEach(item => {
      item.addEventListener('click', function(){
        const arrayClass = [...this.parentElement.querySelector('.video').classList];

        if (arrayClass.includes('playingVideo')) {
          this.parentElement.querySelector('.video').pause();
          this.parentElement.querySelector('.video').classList.remove('playingVideo');
          this.querySelector('.play-video').removeAttribute("hidden");
        }else{
          this.parentElement.querySelector('.video').play();
          this.parentElement.querySelector('.video').classList.add('playingVideo');
          this.querySelector('.play-video').setAttribute("hidden", "");
        }
      });
    });
  }

});

//feature zoom
$(window).on('load', function(){
  if ($(window).width() > 767) {
    $('.img-prod').each(function(){
      $(this).parent().zoom({
          on: 'click',
          url: $(this).attr('src'),
          touch: false
        })
    });

    let resizeHeightSliderThumb = setInterval(function(){
      if ($('.sliderMain').hasClass("slick-initialized") && $('.sliderThumb').hasClass("active-thumb")) {
        let heightSlide = $('.sliderMain .img-prod').height();
        const videos = [...document.querySelectorAll('.video')];
        const str = `<style>.sliderThumb .slick-list{height: ${heightSlide}px !important}</style>`;

        $('.prod-img-thumb').find('style').remove();
        $('.prod-img-thumb').append(str)

        if (videos.length > 0) {
          videos.forEach(item => {
            item.style.maxHeight = heightSlide+'px';
          });
        }
        clearInterval(resizeHeightSliderThumb);
      }
    }, 300);
  }else{
    let resizeHeightSliderMain = setInterval(function(){
      if ($('.sliderMain').hasClass("slick-initialized")) {
        const videos = [...document.querySelectorAll('.video')];

        if (videos.length > 0) {
          videos.forEach(item => {
            item.style.maxHeight = $('.sliderMain .img-prod').height()+'px';
          });
        }
        clearInterval(resizeHeightSliderMain);
      }
    }, 300);
  }
});

let markZoom;
$(window).on('resize', function(){
  if ($(window).width() > 767) {
    if (!markZoom) {
      $('.img-prod').each(function(){
        $(this).parent().zoom({
            on: 'click',
            url: $(this).attr('src'),
            touch: false
          })
      });

      markZoom = true;
    }

    let resizeHeightSliderThumb = setInterval(function(){
      if ($('.sliderMain').hasClass("slick-initialized") && $('.sliderThumb').hasClass("active-thumb")) {
        let heightSlide = $('.sliderMain .img-prod').height();
        const videos = [...document.querySelectorAll('.video')];
        const str = `<style>.sliderThumb .slick-list{height: ${heightSlide}px !important}</style>`;

        $('.prod-img-thumb').find('style').remove();
        $('.prod-img-thumb').append(str)

        if (videos.length > 0) {
          videos.forEach(item => {
            item.style.maxHeight = heightSlide+'px';
          });
        }
        clearInterval(resizeHeightSliderThumb);
      }
    }, 300);
  }else{
    if (markZoom) {
      $('.img-prod').each(function(){
        $(this).parent().trigger('zoom.destroy');
      });
      markZoom = false;
    }

    $('.sliderMain').removeAttr('style');

    let resizeHeightSliderMain = setInterval(function(){
      if ($('.sliderMain').hasClass("slick-initialized")) {
        const videos = [...document.querySelectorAll('.video')];

        if (videos.length > 0) {
          videos.forEach(item => {
            item.style.maxHeight = $('.sliderMain .img-prod').height()+'px';
          });
        }
        clearInterval(resizeHeightSliderMain);
      }
    }, 300);
  }
});

async function resizeVideo(){
  const videos = [...document.querySelectorAll('.video')];

  if (videos.length > 0) {
    videos.forEach(item => {
      item.style.maxHeight = $('.sliderMain .img-prod').height()+'px';
    });
  }
}
