import Minicart from "./minicart"

const Monogram = {

  state: {
    xPos: 0,
    yPos: 0,
    fontSize: 60,
    fontFamily: "serif",
    maxChars: 3,
    colour: "#a07e4b",
    defaultVariant: "39420132556889"
  },

  launchModal: async function (variantId, productHandle, existing = false, element = null) {
    document.body.classList.add("no-scroll");

    let modalMarkup = await fetch(`/products/${productHandle}?view=monogram`)
      .then(fetchResponse => fetchResponse.text());

    let modalContainer = document.createElement("div");
    modalContainer.dataset.target = "monogram_modal";
    modalContainer.innerHTML = modalMarkup;
    document.body.appendChild(modalContainer);

    let jsonData = document.querySelector("#monogramJsonData").textContent;
    let parsedData = JSON.parse(jsonData);
    let selectedVariant = parsedData.variants.find(variant => {
      return variant.id.toString() == variantId.toString()
    })
    let canvas = document.querySelector(".monogram-modal__canvas");
    canvas.style.backgroundImage = `url('${selectedVariant.featured_image.src}')`;

    Object.assign(this.state, {
      xPos: parseInt(parsedData.metafields.xPos) || this.state.xPos,
      yPos: parseInt(parsedData.metafields.yPos) || this.state.yPos,
      fontSize: parseInt(parsedData.metafields.fontSize) || this.state.fontSize,
      fontFamily: parsedData.metafields.fontFamily || this.state.fontFamily,
      maxChars: parseInt(parsedData.metafields.maxChars) || this.state.maxChars,
      colour: parsedData.metafields.colour || this.state.colour
    })

    let monogramKey = `Add up to ${parsedData.metafields.maxCharsText}`;

    let monogramForm = document.querySelector("form[action='/monogram']");
    if (monogramForm) {
      monogramForm.addEventListener("submit", function(event) {
        event.preventDefault();
        let timestamp = + new Date();
        let monogramVariant = parsedData.metafields.monogramVariant?.toString().split("?variant=")[1] || parsedData.metafields.monogramVariant?.toString();
        let itemData = {
          items: [
            {
              id: monogramVariant || Monogram.state.defaultVariant,
              quantity: 1,
              properties: {
                "_pc_pricing_ref": timestamp
              }
            },
            {
              id: variantId,
              quantity: 1,
              properties: {
                [monogramKey]: document.querySelector("#monogramInput").value.toUpperCase(),
                "_pc_pricing_ref": timestamp
              }
            }
          ]
        }
        console.log("itemData:", itemData);
        if (existing) {
          Minicart.addItemToCart(itemData, element);
        } else {
          Minicart.addItemToCart(itemData);
        }
        Monogram.destroyModal();
      })
    }
  },

  destroyModal: async function () {
    let modal = document.querySelector("div[data-target='monogram_modal']");
    if (modal) {
      document.body.removeChild(modal);
      document.body.classList.remove("no-scroll");
    }
  },

  updatePreview: function (input) {
    input.value = input.value.replace(/[^A-Za-z]/g, '');

    let canvas = document.querySelector(".monogram-modal__canvas");
    let context = canvas.getContext("2d");

    context.clearRect(0, 0, canvas.width, canvas.height);

    context.font = `${this.state.fontSize}px ${this.state.fontFamily}`;
    context.textBaseline = "top";
    context.fillStyle = this.state.colour;

    let text = input.value.toUpperCase();
    context.fillText(text, this.state.xPos, this.state.yPos);

    var charSpace = parseInt(this.state.maxChars) - text.length;
    document.querySelector("#charUpdate").innerHTML = charSpace;

    this.maybeEnableButton();
  },

  maybeEnableButton: function () {
    let button = document.querySelector(".monogram-modal__button");
    let input = (document.querySelector(".monogram-modal__input") && document.querySelector(".monogram-modal__input").value.length > 0);
    let checkbox = (document.querySelector(".monogram-modal__checkbox-input") && document.querySelector(".monogram-modal__checkbox-input").checked);
    if (button && input && checkbox) {
      button.removeAttribute("disabled");
    } else if (button) {
      button.setAttribute("disabled", "disabled");
    }
  },

  initialize: async function () {
  }
}

window.Monogram = Monogram;
document.addEventListener("DOMContentLoaded", function () {
  Monogram.initialize();
})

export default Monogram
