import formatMoney from './utils/format-money';

const Minicart = {

  state: {
    drawer: null,
    subtotalDisplay: null,
    items: null,
    itemString: null,
    drawerOpen: false
  },

  submitAtcForm: async function (form) {
    event.preventDefault();
    let itemData = { items: [] };
    const varIdForm = form.querySelector('#pdp-variant-id') != null ? form.querySelector('#pdp-variant-id').value : form.querySelector('.minicart-item-id').value;

    itemData.items.push({
      id: varIdForm,
      quantity: 1,
      properties: {}
    })

    document.querySelector('.minicart__body').scrollTo({
      top: 0,
      behavior: 'smooth'
    });

    setTimeout(function(){
      document.querySelector('.loader').removeAttribute('hidden');
      document.querySelector('.minicart__body').classList.add('remove-scroll');
    }, 500);

    this.addItemToCart(itemData)
  },

  submitAtcFormMono: async function (dataBtn) {
    event.preventDefault();
    let itemData = { items: [] };
    let checkProdHasInCart = false;
    const idProdMono = dataBtn.getAttribute("data-id");
    const idProdNormal = dataBtn.getAttribute("data-id-prod-normal");
    const monoText = dataBtn.closest(".popup-mono").querySelector(".js-type-text").value;

    itemData.items.push({
      id: idProdMono,
      quantity: 1,
      properties: {
        "_monogramming": monoText,
        "_monoProduct": `${idProdNormal}_${monoText}`
      }
    });

    itemData.items.push({
      id: idProdNormal,
      quantity: 1,
      properties: {
        "monogramming": monoText,
        "_mainProduct": `${idProdNormal}_${monoText}`
      }
    });

    this.addItemToCart(itemData);
  },

  addItemToCart: async function (itemData = {}, itemToReplace = false) {
    await fetch('/cart/add.js', {
      method: "POST",
      body: JSON.stringify(itemData),
      headers: {
        'Content-Type': 'application/json; charset=utf-8'
      }
    })
    if (itemToReplace) {
      Minicart.setLineitemQuantity(0, itemToReplace, false, false, true)
    } else {
      await fetch('/cart.js')
      .then(cartResponse => cartResponse.json())
      .then(jsonResponse => {
        let itemString = ''
        for (let item of jsonResponse.items) {
          itemString += `${item.variant_id}:${item.quantity},`
        }
        itemString = itemString.substring(0, itemString.length - 1)
        this.state.itemString = itemString
        this.state.items = jsonResponse.items
        this.state.subtotalDisplay.innerText = formatMoney(jsonResponse.total_price)
        document.getElementById('CartCount').innerText = jsonResponse.item_count

        this.populateCartDrawer(true)
      })
    }
  },

  populateCartDrawer: async function (openDrawer = false) {
    let itemContainer = this.state.drawer.querySelector("#MinicartItemContainer")
    let freeShippingBar = document.querySelector("#MinicartFreeShipping")
    itemContainer.innerHTML = ""
    await fetch('/cart?view=cartitems')
      .then(cartResponse => cartResponse.text())
      .then(htmlResponse => {
        itemContainer.innerHTML = htmlResponse
      })
    await fetch('/cart?view=freeshipping')
      .then(cartResponse => cartResponse.text())
      .then(htmlResponse => {
        if (freeShippingBar) {
          freeShippingBar.innerHTML = htmlResponse
        }
      })

    document.querySelector('.loader').setAttribute('hidden', '');
    document.querySelector('.minicart__body').classList.remove('remove-scroll');

    if (openDrawer) {
      if (window.location.href.indexOf("/cart") > -1) {
        document.querySelector('.cart-alert').classList.add('show');
        window.setTimeout( function() {
          document.querySelector('.cart-alert').classList.remove('show');
        }, 4000);
      } else {
        this.toggleDrawer("open")
      }
      console.log(13242)
    }
  },

  calculateFreeShipping: function(totalPrice){
    if (document.querySelector(".minicart__free-shipping") !== 'null' && document.querySelector(".minicart__free-shipping") != null) {
      const totalFreeShipping = parseInt(document.querySelector(".minicart__free-shipping").getAttribute("data-price-freeshiping"));
      const processBar = ((totalPrice/totalFreeShipping).toFixed(6))*100 > 100 ? 100 : ((totalPrice/totalFreeShipping).toFixed(6))*100;
      const freeShippingRemainder = totalFreeShipping - totalPrice;

      document.querySelector('.progress-bar').style.width = `${processBar}%`;

      if ( freeShippingRemainder > 0 ) {
        document.querySelector(".not-freeshipping").removeAttribute("hidden");
        document.querySelector(".freeshipping").setAttribute("hidden", "");
        document.querySelector('.free_shipping_remainder').innerText = formatMoney(freeShippingRemainder);
      }else{
        document.querySelector(".not-freeshipping").setAttribute("hidden", "");
        document.querySelector(".freeshipping").removeAttribute("hidden");
        document.querySelector('.free_shipping_remainder').innerText = formatMoney(freeShippingRemainder);
      }
    }
  },

  setLineitemQuantity: async function (newQuantity, element, pricingRef = null, replacement = null, reloadCart = false) {
    if (pricingRef && pricingRef != "na") {
      this.handleMonogramQuantity(newQuantity, pricingRef, replacement);
    } else {      
      const keyProduct = element.getAttribute("data-key");
      const codeProduct = element.getAttribute("data-code");
      let keyProductSecond = '';
      let itemDataSecond = {};

      let itemData = {
        id: keyProduct,
        quantity: newQuantity
      }

      element.closest('.item').setAttribute("hidden", "");

      if (codeProduct != '') {
        let idMono;
        const arrayClassElement = [...element.classList]

        if (arrayClassElement.includes("monoProduct")) {
          idMono = [...document.querySelectorAll(".mainProduct")];
        }else{
          idMono = [...document.querySelectorAll(".monoProduct")];
        }

        idMono.forEach(item => {
          if (item.getAttribute("data-code") == codeProduct) {
            item.closest('.item').setAttribute("hidden", "");
            keyProductSecond = item.getAttribute("data-key");
          }
        });

        itemDataSecond = {
          id: keyProductSecond,
          quantity: newQuantity
        }
      }

      await fetch('/cart/change.js', {
        method: "POST",
        body: JSON.stringify(itemData),
        headers: {
          'Content-Type': 'application/json; charset=utf-8'
        }
      }).then(cartResponse => cartResponse.json())
        .then(jsonResponse => {
          if (codeProduct == '') {
            this.state.subtotalDisplay.innerText = formatMoney(jsonResponse.total_price);
            this.calculateFreeShipping(jsonResponse.total_price);

            document.getElementById('CartCount').innerText = jsonResponse.item_count;

            if (jsonResponse.item_count == 0) {
              document.querySelector('.cart-null').removeAttribute('hidden');
            }else{
              document.querySelector('.cart-null').setAttribute('hidden', '');
            }
          }
      })

      if (codeProduct != '') {
        await fetch('/cart/change.js', {
          method: "POST",
          body: JSON.stringify(itemDataSecond),
          headers: {
            'Content-Type': 'application/json; charset=utf-8'
          }
        }).then(cartResponse => cartResponse.json())
          .then(jsonResponse => {
            this.state.subtotalDisplay.innerText = formatMoney(jsonResponse.total_price);
            this.calculateFreeShipping(jsonResponse.total_price);
            document.getElementById('CartCount').innerText = jsonResponse.item_count;

            if (jsonResponse.total_price == 0) {
              document.querySelector('.cart-null').removeAttribute('hidden');
            }else{
              document.querySelector('.cart-null').setAttribute('hidden', '');
            }
        })
      }
    }

    if (reloadCart) {
      await fetch('/cart.js')
      .then(cartResponse => cartResponse.json())
      .then(jsonResponse => {
        let itemString = ''
        for (let item of jsonResponse.items) {
          itemString += `${item.variant_id}:${item.quantity},`
        }
        itemString = itemString.substring(0, itemString.length - 1)
        this.state.itemString = itemString
        this.state.items = jsonResponse.items
        this.state.subtotalDisplay.innerText = formatMoney(jsonResponse.total_price)
        document.getElementById('CartCount').innerText = jsonResponse.item_count

        this.populateCartDrawer(true)
      })
    }
  },

  handleMonogramQuantity: async function(newQuantity, pricingRef, replacement) {
    let jsonData = document.querySelector("#monogramCartData").textContent;
    let parsedData = JSON.parse(jsonData);
    let matchingLines = [];
    parsedData.items.forEach(item => {
      if (item.pricingRef == pricingRef) {
        matchingLines.push({
          id: item.key,
          quantity: newQuantity
        });
      }
    });
    for (let line of matchingLines) {
      await fetch('/cart/change.js', {
        method: "POST",
        body: JSON.stringify(line),
        headers: {
          "Content-Type": "application/json; charset=utf-8"
        }
      })
    }
    if (replacement) {
      this.addItemToCart({
        id: replacement,
        quantity: 1,
        properties: {}
      })
    } else {
      await fetch('/cart.js')
      .then(cartResponse => cartResponse.json())
      .then(jsonResponse => {
        let itemString = ''
        for (let item of jsonResponse.items) {
          itemString += `${item.variant_id}:${item.quantity},`
        }
        itemString = itemString.substring(0, itemString.length - 1)
        this.state.itemString = itemString
        this.state.items = jsonResponse.items
        this.state.subtotalDisplay.innerText = formatMoney(jsonResponse.total_price)
        document.getElementById('CartCount').innerText = jsonResponse.item_count

        this.populateCartDrawer(true)
      })
    }
  },

  increaseQty: function(element){
    const elementQty = element.parentElement.querySelector('.qty-product');
    const qtyProduct = parseInt(elementQty.value) + 1;
    const keyProduct = element.getAttribute("data-key");

    elementQty.value = qtyProduct;

    let itemData = {
      id: keyProduct,
      quantity: qtyProduct
    }

    fetch('/cart/change.js', {
      method: "POST",
      body: JSON.stringify(itemData),
      headers: {
        'Content-Type': 'application/json; charset=utf-8'
      }
    }).then(cartResponse => cartResponse.json())
      .then(jsonResponse => {
        this.state.subtotalDisplay.innerText = formatMoney(jsonResponse.total_price);
        this.calculateFreeShipping(jsonResponse.total_price);
        document.getElementById('CartCount').innerText = jsonResponse.item_count;
    })
  },

  decreaseQty: async function(element){
    const elementQty = element.parentElement.querySelector('.qty-product');
    const qtyProduct = (parseInt(elementQty.value) - 1) < 0 ? 0 : parseInt(elementQty.value) - 1;
    const keyProduct = element.getAttribute("data-key");
    const codeProduct = element.getAttribute("data-code")
    let keyProductSecond = '';
    let itemDataSecond = {};

    elementQty.value = qtyProduct;

    if (qtyProduct == 0) {
      element.closest('.item').setAttribute('hidden', '');

      if (codeProduct != '') {
        let idMono;
        const arrayClassElement = [...element.classList];

        if (arrayClassElement.includes("monoProductDesc")) {
          idMono = [...document.querySelectorAll(".mainProductDesc")];
        }else{
          idMono = [...document.querySelectorAll(".monoProductDesc")];
        }

        idMono.forEach(item => {
          if (item.getAttribute("data-code") == codeProduct) {
            keyProductSecond = item.getAttribute("data-key");
            item.closest('.item').setAttribute('hidden', '');
          }
        });

        itemDataSecond = {
          id: keyProductSecond,
          quantity: 0
        }
      }
    }

    let itemData = {
      id: keyProduct,
      quantity: qtyProduct
    }

    await fetch('/cart/change.js', {
      method: "POST",
      body: JSON.stringify(itemData),
      headers: {
        'Content-Type': 'application/json; charset=utf-8'
      }
    }).then(cartResponse => cartResponse.json())
      .then(jsonResponse => {
        if (qtyProduct != 0 || codeProduct == '') {
          this.state.subtotalDisplay.innerText = formatMoney(jsonResponse.total_price)
          this.calculateFreeShipping(jsonResponse.total_price)
          document.getElementById('CartCount').innerText = jsonResponse.item_count;

          if (jsonResponse.total_price == 0) {
            document.querySelector('.cart-null').removeAttribute('hidden');
          }else{
            document.querySelector('.cart-null').setAttribute('hidden', '');
          }
        }
    })

    if (codeProduct != '' && qtyProduct == 0) {
     fetch('/cart/change.js', {
        method: "POST",
        body: JSON.stringify(itemDataSecond),
        headers: {
          'Content-Type': 'application/json; charset=utf-8'
        }
      }).then(cartResponse => cartResponse.json())
        .then(jsonResponse => {
          this.state.subtotalDisplay.innerText = formatMoney(jsonResponse.total_price)
          this.calculateFreeShipping(jsonResponse.total_price)
          document.getElementById('CartCount').innerText = jsonResponse.item_count;

          if (jsonResponse.total_price == 0) {
            document.querySelector('.cart-null').removeAttribute('hidden');
          }else{
            document.querySelector('.cart-null').setAttribute('hidden', '');
          }
      })
    }
  },

  toggleDrawer: function (state = "toggle") {
    let drawer = this.state.drawer
    let drawerOpen = this.state.drawerOpen
    switch (state) {
      case "open":
        drawer.classList.add("open");
        document.body.style.overflowY = "hidden";
        this.state.drawerOpen = true;

        if (document.getElementById("popup-mono") != null) {
          document.getElementById("popup-mono").style.opacity = '0';
          setTimeout(function(){
            document.getElementById("popup-mono").setAttribute('hidden', '')
          }, 300);
          document.querySelector('.js-type-text').value = "";
          document.querySelector('.js-addToCartMono').classList.add('disable');
          document.getElementById("tern-mono").checked = false;
        }
        break
      case "close":
        drawer.classList.remove("open")
        document.body.style.overflowY = "auto"
        this.state.drawerOpen = false
        break
      default:
        drawerOpen ? drawer.classList.remove("open") : drawer.classList.add("open")
        drawerOpen ? document.body.style.overflowY = "auto" : document.body.style.overflowY = "hidden"
        this.state.drawerOpen = !drawerOpen
    }
  },

  initialize: async function () {
    // Set state
    let itemString = ""
    fetch('/cart.js')
      .then(cartResponse => cartResponse.json())
      .then(jsonResponse => {
        for (let item of jsonResponse.items) {
          itemString += `${item.variant_id}:${item.quantity},`
        }
        itemString = itemString.substring(0, itemString.length - 1)
        this.state.itemString = itemString
        this.state.items = jsonResponse.items
        this.populateCartDrawer()
      })

    //Add to cart normal
    const btnAddToCart = [...document.querySelectorAll(".js-add-to-cart")];

    btnAddToCart.forEach(btn => {
      btn.addEventListener('click', function(){
        Minicart.submitAtcForm(this.closest(".form-ATC"));
      });
    });

    //Add to cart with monogramming
    if (document.querySelector('.js-addToCartMono') != null) {
      document.querySelector('.js-addToCartMono').addEventListener('click', function(e){
        e.preventDefault();
        Minicart.submitAtcFormMono(this);
      });
    }

    // Sets remaining Minicart.state properties
    this.state.drawer = document.querySelector('#MinicartDrawer')
    this.state.subtotalDisplay = document.querySelector('#MinicartSubtotal')

    // Add event to close when clicking outside minicart drawer
    this.state.drawer.addEventListener("click", function () {
      let minicartMain = this.querySelector(".minicart__main")
      if (minicartMain && !minicartMain.contains(event.target)) {
        Minicart.toggleDrawer("close")
      }
    })
  }

}

window.Minicart = Minicart
document.addEventListener("DOMContentLoaded", function () {
  Minicart.initialize();
})

export default Minicart
