const Product = {

  /* Maintains the current state of the product, this will update itself as the user takes action */
  state: {
    variants: null,
    selectedVariant: null,
    lastSelections: {
      option1: null,
      option2: null,
      option3: null
    },
    handle: null,
    thumbnailContainer: null,
    activeImage: null,
    activeImageIndex: 0
  },

  /**
   * Maintains the user selection and sets the hidden variant ID in the ATC form
   * @param {Element} selector - The selector that triggered this function
   */
  updateSelection: function (selector) {
    const optionSelectors = [...document.querySelectorAll('.pdp-option-select')];
    const labelVariants = [...document.querySelectorAll('.name-option')];

    optionSelectors.forEach(optionSelector => {
      if (optionSelector.checked) {
        this.state.lastSelections[optionSelector.dataset.option] = optionSelector.value;
      }
    });

    let possibleVariants = this.state.variants.filter(variant => {
      return (
        variant.option1 == this.state.lastSelections.option1
        && variant.option2 == this.state.lastSelections.option2
        && variant.option3 == this.state.lastSelections.option3
      )
    })
    console.log(this.state.variants)

    if (possibleVariants.length > 0) {
      let availableProduct = possibleVariants[0].available;

      // When only one variant matches user selections, that must be the selected variant
      if (possibleVariants.length == 1) {
        this.state.selectedVariant = possibleVariants[0]
        document.getElementById("pdp-variant-id").value = this.state.selectedVariant.id;
        document.querySelector(".js-addToCartMono").setAttribute('data-id-prod-normal', this.state.selectedVariant.id);
        let priceDisplay = document.querySelector(".price-original");
        if (priceDisplay) {
          let thisPrice = `${priceDisplay.dataset.currency || "£"}${(parseFloat(this.state.selectedVariant.price) / 100).toFixed(2)}`;
          priceDisplay.innerText = thisPrice;
        }
      }

      if ([...document.querySelectorAll('.js-add-to-cart')].length > 0) {
        document.querySelectorAll('.js-add-to-cart').forEach(item => {
          item.classList.remove('disabled');
        });
      }


      if ([...document.querySelectorAll(".js-popup-mono")].length > 0) {
        document.querySelectorAll(".js-popup-mono").forEach(item => {
          item.classList.remove('disabled');
          item.setAttribute('data-variant', this.state.lastSelections.option1.toLowerCase());
        });
      }

      if (!availableProduct) {
        if (labelVariants.length > 0) {
          labelVariants[0].classList.add("not-available");
        }
        document.querySelector('form.notify-me-form').classList.remove('d-none');
        if ([...document.querySelectorAll('.btn-out-of-stock')].length > 0) {
          document.querySelectorAll('.btn-out-of-stock').forEach(item => {
            item.removeAttribute('hidden');
          });
        }

        if ([...document.querySelectorAll('.section-submit')].length > 0) {
          document.querySelectorAll('.section-submit').forEach(item => {
            item.setAttribute('hidden', '');
          });
        }

        document.querySelector('.message-out-of-stock').removeAttribute("hidden");
        document.querySelector('.status-qty').removeAttribute("hidden");
        document.querySelector('.product-info .description').setAttribute("hidden", "");
        document.querySelector('.price-original').classList.add("out-of-stock");
        if (document.querySelector('.js-variant-field')) document.querySelector('.js-variant-field').value = selector.value;
        document.querySelector('.name-variant').innerText = [...document.querySelectorAll('.pdp-option-select')][0].getAttribute('data-option-name');
      }else{
        if (labelVariants.length > 0) {
          labelVariants[0].classList.remove("not-available");
        }

        document.querySelector('form.notify-me-form').classList.add('d-none');

        if ([...document.querySelectorAll('.btn-out-of-stock')].length > 0) {
          document.querySelectorAll('.btn-out-of-stock').forEach(item => {
            item.setAttribute('hidden', '');
          });
        }

        if ([...document.querySelectorAll('.section-submit')].length > 0) {
          document.querySelectorAll('.section-submit').forEach(item => {
            item.removeAttribute('hidden');
          });
        }

        document.querySelector('.message-out-of-stock').setAttribute("hidden", "");
        document.querySelector('.status-qty').setAttribute("hidden", "");
        document.querySelector('.product-info .description').removeAttribute("hidden");
        document.querySelector('.price-original').classList.remove("out-of-stock");
      }
    }else {
      if ([...document.querySelectorAll(".js-popup-mono")].length > 0) {
        document.querySelectorAll(".js-popup-mono").forEach(item => {
          item.classList.add('disabled');
        });
      }

      if ([...document.querySelectorAll('.js-add-to-cart')].length > 0) {
        document.querySelectorAll('.js-add-to-cart').forEach(item => {
          item.classList.add('disabled');
        });
      }
    }
  },

  /**
   * Returns a JSON representation of the passed product URL.
   * @param {String} productUrl - The URL for the product you want the data from. Does not need ?view parameter. Can be /products/path
   */
  getData: async function (productUrl) {
    let thisProductData = null
    await fetch(`${productUrl}?view=json`)
      .then(productResponse => productResponse.json())
      .then(jsonResponse => {
        thisProductData = jsonResponse
      })
    return thisProductData
  },

  /**
   * Sets the src of the main image to the src of the passed imageElement, and sets height of thumbnail container
   * @param {Element} imageElement - The DOM element of the new image (thumbnail)
   */
  setActiveImage: function (imageElement) {
    // Set featured image src - State active image does not change (same element), but active image index will
    this.state.activeImage.src = imageElement.src
    this.state.activeImageIndex = imageElement.dataset.index

    // Set thumbnail viewer height to match new featured image height if viewport width > 767px or auto if viewport width <= 767px
    let viewportWidth = Math.max(document.documentElement.clientWidth || 0, window.innerWidth || 0)
    if (viewportWidth > 767) {
      let imageHeight = this.state.activeImage.offsetHeight
      this.state.thumbnailContainer.style.height = imageHeight + "px"
    } else {
      this.state.thumbnailContainer.style.height = "auto"
    }
  },

  /**
   * Initializes a product page
   *    Sets the state variants of this product
   *    Sets state handle of this product
   *    Adds event listeners to all option selectors
   *        The event listener updates the user selection
   *        For swatches, the event listener also displays the active selection
   *    Sets up image gallery and thumbnail container
   *    Setups up product tabs (if necessary)
   *    Adds this product to users recently viewed products.
   * @param {String} productPath - The path to the product e.g. /products/a-blue-dress
   */
  initialize: async function (productPath) {
    // Set state variants
    let checkOptionVariant = [...document.querySelectorAll('.option-variant')];
    await fetch(`${productPath}?view=json`)
      .then(productResponse => productResponse.json())
      .then(jsonResponse => {
        this.state.variants = jsonResponse.variants
      })

    // Set state handle
    this.state.handle = productPath.split("/").slice(-1).pop()

    // Sets event listeners on option selectors
    let optionSelectors = Array.from(document.querySelectorAll('.pdp-option-select'));
    for (let optionSelector of optionSelectors) {
      optionSelector.addEventListener("change", function () {
        if (this.type == "radio") {
          let thisLabel = this.parentElement
          let thisOptionContainer = thisLabel.parentElement
          let thisOptionSiblings = Array.from(thisOptionContainer.querySelectorAll("label"))
          for (let optionSiblingLabel of thisOptionSiblings) {
            optionSiblingLabel.classList.remove("selected-option")
          }
          thisLabel.classList.add("selected-option");
          this.closest('.option-variant').querySelector('.name-option').innerText = this.value;
        }
        Product.updateSelection(this)
        window.setTimeout(function() {
          if(document.querySelectorAll('.option-variant.fabric label').length > 1) {
            if (document.querySelector('.option-variant.fabric .selected-option').classList.contains('disabled-option')) {
              document.querySelector('.option-variant.fabric .pdp-option-swatch:not(.disabled-option)').click();
              console.log(document.querySelector('.option-variant.fabric .pdp-option-swatch:not(.disabled-option)'))
            }
          }
        }, 300)
      })
    }

    Product.updateSelection(optionSelectors[0]);

    //check vairant by color
    if (checkOptionVariant.length > 1) {
      let optionSelectors1 = [...document.querySelectorAll('.pdp-option-select[name="option1"]')];
      let optionSelectors2 = [...document.querySelectorAll('.pdp-option-select[name="option2"]')];
      let arrayVariants = this.state.variants;

      optionSelectors2.forEach(option2 => {
        option2.parentElement.classList.add('disabled-option');
      });

      arrayVariants.forEach(variant => {
        if (variant.option1 == optionSelectors1[0].value) {
          const prodVarOption2 = variant.option2;

          optionSelectors2.forEach(option2 => {
            if (prodVarOption2 == option2.value) {
              option2.parentElement.classList.remove('disabled-option');
            }
          });
        }
      });

      //event change option1 will check variant option2
      optionSelectors1.forEach(optionSelector => {
        optionSelector.addEventListener('change', function(){
          const valOption1 = this.value;

          optionSelectors2.forEach(option2 => {
            option2.parentElement.classList.add('disabled-option');
          });

          arrayVariants.forEach(variant => {
            if (variant.option1 == valOption1) {
              const prodVarOption2 = variant.option2;

              optionSelectors2.forEach(option2 => {
                if (prodVarOption2 == option2.value) {
                  option2.parentElement.classList.remove('disabled-option');
                }
              });
            }
          });
        });
      });
    }

    // Sets up image gallery
    if (document.getElementById("ProductThumbnailContainer")) {
      // Add click event to all thumbnails to change main image
      this.state.thumbnailContainer = document.getElementById("ProductThumbnailContainer")
      let allThumbnails = Array.from(this.state.thumbnailContainer.querySelectorAll(".product-thumbnail"))
      for (let thumbnail of allThumbnails) {
        thumbnail.addEventListener("click", function () {
          Product.setActiveImage(this)
        })
      }

      // Set the initial activeImage state and then run setActiveImage to scale the thumbnail container
      this.state.activeImage = document.getElementById("ProductActiveImage")
      this.setActiveImage(this.state.activeImage)

      // Run setActiveImage on resize to rescale thumbnail container
      window.addEventListener('resize', function () {
        Product.setActiveImage(Product.state.activeImage)
      });
    }

    // Sets up product tab headers
    if (document.querySelector(".tabbed-headers")) {
      let tabHeaders = Array.from(document.querySelectorAll(".tabbed-headers label"))
      for (let thisHeader of tabHeaders) {
        thisHeader.addEventListener("click", function () {
          for (let header of tabHeaders) {
            header.classList.remove("active", "text-primary")
          }
          thisHeader.classList.add("active", "text-primary")
        })
      }
    }

    this.setupStickySubmit();
  },

  setupStickySubmit: function () {
    let stickyGroup = document.querySelector(".stickySubmit");
    if (!stickyGroup) return;
    let observer = new IntersectionObserver(function (entries) {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          stickyGroup.classList.add("sticky");
        } else {
          stickyGroup.classList.remove("sticky");
        }
      });
    });
    observer.observe(stickyGroup);
    console.log("observing")
  }

}

window.Product = Product
document.addEventListener("DOMContentLoaded", function () {
  if (ShopifyAnalytics && ShopifyAnalytics.meta.page.resourceType == "product") {
    let productPath = window.location.pathname
    Product.initialize(productPath)
  }
})


export default Product
