import Swiper, { Navigation, Pagination, Autoplay, Scrollbar, Thumbs } from "swiper/core";
Swiper.use([Navigation, Pagination, Scrollbar, Autoplay, Thumbs]);

window.addEventListener("DOMContentLoaded", () => {
   // Announcement bar
   const header = document.querySelector(".header-outer");
   const announcementBar = document.getElementById("AnnouncementBar");
   if (announcementBar) {
      $(announcementBar).slick({
         slidesToShow: 1,
         slidesToScroll: 1,
         autoplay: true,
         autoplaySpeed: Number(announcementBar.getAttribute("data-speed")),
         arrows: false
      })

      window.addEventListener("scroll", () => {
         const stackable = document.querySelector('.stackable');
         if (document.body.scrollTop > announcementBar.offsetHeight || document.documentElement.scrollTop > announcementBar.offsetHeight) {
            announcementBar.parentNode.style.opacity = 0;
            header.classList.add("header-outer--sticky");
            if (stackable) {
               header.classList.add("header-outer--sticky-stackable");
            }
         } else {
            announcementBar.parentNode.style = null;
            header.classList.remove("header-outer--sticky");
            if (stackable) {
               header.classList.remove("header-outer--sticky-stackable");
            }
         }
      })
   }

   // Tab
   const tabElement = [...document.querySelectorAll("[tab-wrapper]")];

   if (tabElement) {
      tabElement.forEach(item => {
         var navigatorWrapper = item.querySelector("[tab-navigators]");
         var contentWrapper = item.querySelector("[tab-content]");
         var navigators = [...navigatorWrapper.getElementsByTagName("a")];

         let contents = contentWrapper.childNodes;
         let maxHeight = 0;
         for (var i=0; i<contents.length; i++) {
            if (contents[i].offsetHeight > maxHeight) {
               maxHeight = contents[i].offsetHeight;
            }
         }

         if (item.getAttribute("autoplay") === "true" && item.getAttribute("speed")) {
            setInterval(() => {
               let currentNav = navigatorWrapper.querySelector(".active");
               if (currentNav.nextElementSibling) {
                  currentNav.nextElementSibling.click();
               }
               else {
                  navigatorWrapper.firstElementChild.click();
               }
            }, Number(item.getAttribute("speed")));
         }
         
         navigators.forEach(navigator => {
            navigator.addEventListener("click", (e) => {
               let currentNav = navigatorWrapper.querySelector(".active");
               let currentActive = contentWrapper.querySelector(".active");
               if (currentActive) {
                  currentActive.classList.remove("active");
                  currentNav.classList.remove("active");
               }
               let tabContent = document.getElementById(e.currentTarget.getAttribute("target"));
               tabContent.style.height = maxHeight + "px";
               tabContent.classList.add("active");
               navigator.classList.add("active");
            })
         })
      })
   }

   // Slider
   const sliderArrow = `<svg width="11" height="20" viewBox="0 0 11 20" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M0.793274 1.35864L9.3769 9.94227" stroke="#080C22" stroke-width="1.57111" stroke-linecap="round"/><path d="M9.37646 9.94238L0.792836 18.526" stroke="#080C22" stroke-width="1.57111" stroke-linecap="round"/></svg>`;
   const indexSlider = document.querySelectorAll('[index-slider-wrapper]');

   if (indexSlider) {
      indexSlider.forEach(item => {
         let slide = new Swiper(item, {
            slidesPerView: 2.2,
            spaceBetween: 8,
            watchSlidesProgress: true,
            autoHeight: true,
            navigation: {
              nextEl: '.next-icon',
              prevEl: ".prev-icon",
            },
            loop: true,
            breakpoints: {
               899: {
                  spaceBetween: 17,
                  slidesPerView: 3.1,
               }
            }
         });
      })
   }

   // Video
   const videoElement = document.querySelectorAll("[video-wrapper]");
   if (videoElement) {
      videoElement.forEach(wrapper => {
         let unmuteBtn = wrapper.querySelector("[video-sound-toggle]");
         unmuteBtn.addEventListener("click", () => toggleSoundVideo(wrapper));
      })
   }
});

// Video
function toggleSoundVideo(videoWrapper) {
   let video_type = videoWrapper.getAttribute("data-video-type");
   videoWrapper.classList.toggle("unmuted");

   switch(video_type) {
      case 'self_host': {
         let video = videoWrapper.getElementsByTagName("video")[0];
         video.muted = !video.muted;
         break;
      }
      case 'youtube': {
         let video = videoWrapper.getElementsByTagName("iframe")[0];
         let is_mute = video.src.match(/mute=\d/)[0].match(/\d/);
         video.src = video.src.replace(`mute=${is_mute}`, `mute=${is_mute == '1' ? '0' : '1' }`);
         break;
      }
      case 'vimeo': {
         let video = videoWrapper.getElementsByTagName("iframe")[0];
         let is_mute = video.src.match(/muted=\d/)[0].match(/\d/);
         video.src = video.src.replace(`mute=${is_mute}`, `mute=${is_mute == '1' ? '0' : '1' }`);
         break;
      }
   }
}